.nav-bar {
	position: relative;
	left: 0px;
	top: 0px;
	width: 100%;
	transition: all 500ms ease;
	-moz-transition: all 500ms ease;
	-webkit-transition: all 500ms ease;
	-ms-transition: all 500ms ease;
	-o-transition: all 500ms ease;
	.header-upper {
		position: relative;
		transition: all 500ms ease;
		-moz-transition: all 500ms ease;
		-webkit-transition: all 500ms ease;
		-ms-transition: all 500ms ease;
		-o-transition: all 500ms ease;
		z-index: 5;
		.auto-container {
			position: static;
			max-width: 1200px;
			padding: 0px 15px;
			margin: 0 auto;
			.logo-outer {
				position: relative;
				z-index: 25;
				text-align: center;
				padding: 32px 0px;
				.logo {
					position: relative;
					a {
						position: relative;
						display: inline-block;
						text-decoration: none;
						outline: none;
					}
				}
			}
		}
	}
	.header-lower {
		position: relative;
		border-top: 1px solid #f5f4f4;
		.auto-container {
			position: static;
			max-width: 1200px;
			padding: 0px 15px;
			margin: 0 auto;
			.nav-outer {
				position: relative;
				.main-menu {
					position: relative;
					.navbar-collapse {
						padding: 0px;
						.nav-menu {
							position: relative;
							margin: 0px;
							list-style: none;
							li {
								position: relative;
								float: left;
								padding: 0px;
								border-left: 1px solid #f5f4f4;
								a {
									position: relative;
									display: block;
									padding: 17px 13px;
									font-size: 12px;
									color: #777777;
									line-height: 30px;
									font-weight: 500;
									opacity: 1;
									letter-spacing: 1px;
									text-transform: uppercase;
									transition: all 500ms ease;
									-moz-transition: all 500ms ease;
									-webkit-transition: all 500ms ease;
									-ms-transition: all 500ms ease;
									-o-transition: all 500ms ease;
									text-decoration: none;
									&::before {
										content: "";
										position: absolute;
										z-index: -1;
										left: 0;
										right: 0;
										bottom: 0;
										background: #fa2964;
										height: 4px;
										opacity: 0;
										-webkit-transform: translateY(4px);
										transform: translateY(4px);
										-webkit-transition-property: transform;
										transition-property: transform;
										-webkit-transition-duration: 0.3s;
										transition-duration: 0.3s;
										-webkit-transition-timing-function: ease-out;
										transition-timing-function: ease-out;
									}
								 &:hover {
									 color: #fa2964;
								 }
								}
							}
						}
					}
				}
			}
		}
	}
}
